'use strict';jQuery(window).on('load',function(){console.log('window loaded');var aboutSection=document.querySelector('.about-section-container');if(aboutSection){var tabBtns=aboutSection.querySelectorAll('.tab-btns');var tabContents=aboutSection.querySelectorAll('.tab-content-container');var pageAddress=window.location.href;var currentPage=window.location.origin+'/about/';var address=void 0;if(pageAddress==currentPage){address="#not-found-btn";}else{address=pageAddress.replace(''+currentPage,'');}var activeBtn=aboutSection.querySelector('button'+address);if(activeBtn){setTimeout(function(){activeBtn.click();window.scrollTo(0,0);},300);}tabBtns.forEach(function(btn){return btn.addEventListener('click',function(e){e.preventDefault();var contClass=btn.dataset.targetContent;var cont=aboutSection.querySelector('.'+contClass);tabBtns.forEach(function(item){return item.classList.remove('active');});btn.classList.add('active');tabContents.forEach(function(container){return container.classList.remove('active');});cont.classList.add('active');});});}});
